@use '../abstracts' as *;

#cta {
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    @media #{$break1} {
        padding-block: 4rem;
        background-image: url('../assets/images/shared/bg-beta-mobile.jpg');
    }
    @media #{$break2} {
        background-image: url('../assets/images/shared/bg-beta-tablet.jpg');
    }
    @media #{$break2open} {
        padding-block: 4.25rem;
    }
    @media #{$break3open} {
        background-image: url('../assets/images/shared/bg-beta-desktop.jpg');
    }
}

#cta-title {
    max-inline-size: pxToRem(400);

